<template>
  <div class="home">
      <div id="left">
        <button @click='changeGraphWidth'>{{graphCols}}</button>
        <StatsDisplay v-bind:highlightSet='highlightSet' :style='graphStyles'> </StatsDisplay>
      </div>
      <div id="right">
            <div v-for="( item, index ) in scatterData" :key='index' class="chartbox" :style='graphStyles'>
                <CovidScatter ref='test' v-bind:chart-id='"test" + index' v-bind:chartData='item.scatter' v-bind:highlightSet='highlightSet' v-bind:referenceLines='item.referenceLines'></CovidScatter>
            </div>
        </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import CovidScatter from '@/components/CovidScatter.vue' // @ is an alias to /src
import StatsDisplay from '@/components/StatsDisplay.vue'
import { DataFactory, ScatterChartComponents, ReferenceLine, LocationLevel } from '@/assets/CovidData'
import { HighlightData } from '@/assets/HighlightSystem'
import { LocationsMap } from '@/assets/LocationsMap'

Array<ScatterChartComponents>()

export default defineComponent({
  name: 'Multi',
  components: {
    CovidScatter,
    StatsDisplay
  },
  data () {
    return {
      scatterData: new Array<ScatterChartComponents>(),
      highlightSet: {},
      locationsMap: {},
      graphCols: 2
    }
  },
  computed: {
    graphStyles: function (): object {
      return {
        '--graph-width': Math.floor(100 / this.graphCols) + '%'
      }
    }
  },
  methods: {
    changeGraphWidth: function () {
      this.graphCols++
      if (this.graphCols > 3) {
        this.graphCols = 1
      }
    },
    draw: async function () {
      const locMap = await LocationsMap.getInstance()
      this.locationsMap = locMap
      this.highlightSet = new HighlightData(locMap)

      const graphs = [
        { event: 'confirmed_states', series: '_delta_ma_7' },
        { event: 'confirmed', series: '_delta_ma_7' },
        { event: 'confirmed_states', series: 'total' },
        { event: 'confirmed', series: 'total' },
        { event: 'deaths_states', series: 'total' },
        { event: 'deaths', series: 'total' },
        { event: 'cfr', series: 'state' },
        { event: 'cfr', series: 'county' }
      ]
      for (let idx = 0; idx < graphs.length; idx++) {
        const components = await this.getData(graphs[idx].event, graphs[idx].series)
        console.log(components)
        this.scatterData.push(components)
      }
      // ADD CFRs
    },
    getData: async function (event: string, series: string) {
      const df = DataFactory.getInstance()
      if (event === 'cfr') {
        const level = (series === 'county') ? LocationLevel.County : LocationLevel.State

        const data = await df.getCFR(level)
        const refLines = await df.getReferenceLines('cfr_' + series, '_total')
        return { scatter: data, referenceLines: refLines }
      } else {
        const data = await df.getPopDataSet(event, series)
        const refLines = await df.getReferenceLines(event, series)
        return { scatter: data, referenceLines: refLines }
      }
    }
  },
  mounted () {
    this.draw()
  }
})
</script>
// work on highlight mechanism

<style scoped>
.chartbox {
  width: var(--graph-width);
  float: left;
  background-Color: white;
}
.display {
  width: 100%;
}
#home  {
  background-Color: #101010;
}
#right {
  float: left;
  width: 90%
}
#left {
  float: left;
  width: 10%;
}</style>

