export interface Point {
  x: number;
  y: number;
}

export interface ScatterPoint extends Point {
  r: number;
}

export enum LocationLevel {
  County,
  State,
  National
}

export interface ChartSet {
  data: Array<Point>;
  xlabel: string;
  ylabel: string;
  title: string;
  day: string;
  locations: Array<number>;
}

export interface ScatterChartComponents {
  scatter: ChartSet;
  referenceLines: Array<ReferenceLine>;
}

export interface ScatterSet extends ChartSet {
  data: Array<ScatterPoint>;
}

export interface ReferenceLine {
  slope: number;
  label: string;
}

// Singleton
export class DataFactory {
  private static instance: DataFactory;
  private _source: string;
  private _backup: string;
  private _external: string;
  private _day: string;

  private constructor () {
    this._source = 'file:///'
    this._source = 'http://127.0.0.1:8081/'
    this._backup = 'http://192.168.1.200:8081/'
    this._source = 'http://192.168.1.200/covidcache/'
    this._external = 'https://raw.githubusercontent.com/yehster/covid-data-prelim/main/'
    this._source = this._external
    this._day = ''
  }

  private async retrData (url: string) {
    const res = await fetch(this._source + url)
    return await res.json()
  }

  private retrieveData (url: string) {
    return new Promise<any>((resolve, reject) => {
      function finish (response: Response) {
        response.json().then((data) => { resolve(data) })
      }
      fetch(this._source + url).then((response) => {
        if (response.ok) {
          finish(response)
        } else {
          fetch(this._backup + url).then(finish)
        }
      })
    })
  }

  public static getInstance () {
    if (!DataFactory.instance) {
      DataFactory.instance = new DataFactory()
    }
    return DataFactory.instance
  }

  public async getAsOf () {
    if (this._day === '') {
      const data = await this.retrieveData('as-of')
      this._day = data
    }
  }

  public async getReferenceLines (event: string, series: string) {
    // label series 'total' for ''
    await this.getAsOf()
    const url = 'pop-reference-lines/' + event + '/' + series + '/' + this._day
    const data = await this.retrieveData(url)
    const retVal = data as Array<ReferenceLine>
    return retVal
  }

  public async getPopDataSet (event: string, series: string) {
    await this.getAsOf()
    const url = 'pop-cases/' + event + '/' + series + '/' + this._day
    // TODO: add day parameter!
    const ChartData = await this.retrieveData(url)
    return {
      data: ChartData.points,
      locations: ChartData.locations,
      xlabel: ChartData.xlabel,
      title: 'Hello world',
      day: ChartData.day,
      ylabel: event
    }
  }

  public async getCFR (level: LocationLevel) {
    const levelString = (level === LocationLevel.County) ? 'county' : 'states'
    const lag = 0
    const url = 'case-fatality-rates/' + lag + '/' + this._day + '/' + levelString
    const ChartData = await this.retrieveData(url)
    return {
      data: ChartData.points,
      locations: ChartData.locations,
      xlabel: ChartData.xlabel,
      title: 'CFR',
      day: ChartData.day,
      ylabel: 'deaths'
    }
  }

  public async getTimeSeries (level: LocationLevel, location: number, event: string, series: string) {
    return 'TODO data'
  }
}
