import { LocationData, LocationsMap } from '@/assets/LocationsMap'

interface Notify {
  (): void;
}

enum FocusLevel {
  none,
  state,
  county,
  hover
}

// const palette = [0x003f5c, 0x2f4b7c, 0x665191, 0xa05195, 0xd45087, 0xf95d6a, 0xff7c43, 0xffa6003]
// const palette = [0x003f5c, 0x7a5195, 0xef5675, 0xffa600]
const palette = [0x003f5c, 0x58508d, 0xbc5090, 0xff6361, 0xffa600]
function paletteToRGB (pal: number) {
  const b = pal % 256
  const g = (pal >> 8) % 256
  const r = (pal >> 16) % 256
  return 'rgb(' + r + ',' + g + ',' + b + ')'
}

const gray = 0xBEBEBE as number
const grayString = paletteToRGB(gray)

interface HighlightInfo {
  rgb: number;
  colorString: string;
}

export interface HighlightLocation {
  location: LocationData;
  highlight: HighlightInfo;
}

export class HighlightData {
  private _stateHighlight: number
  private _locationHighlight: number
  private _stateGroup: Record<number, HighlightInfo>
  private _subscribers: Array<Notify>
  private _debounceTimer: ReturnType<typeof setTimeout> | null
  private _stateColorMap: Record<number, string>
  private _locations: LocationsMap
  private static _lastNotify: Notify | null = null
  private _lastColor = 0

  private initColorMap () {
    const retVal: Record<number, string> = {}
    for (let idx = 0; idx < this._locations.stateList().length; idx++) {
      const curState = this._locations.stateList()[idx]
      const palatteIDX = curState.popRank % (palette.length)
      retVal[curState.state] = paletteToRGB(palette[palatteIDX])
    }
    return retVal
  }

  constructor (locations: LocationsMap) {
    this._stateHighlight = 15
    this._locationHighlight = 0
    this._subscribers = new Array<Notify>()
    this._stateGroup = {}
    this._debounceTimer = null
    this._locations = locations
    this._stateColorMap = this.initColorMap()
  }

  public locationColor (loc: LocationData): string {
    if ((this._locationHighlight === loc.id)) {
      return 'rgba(30,230,30)'
    }

    if (typeof this._stateGroup[loc.state] === 'undefined') {
      return 'rgba(190,190,190)'
    } else {
      return this._stateGroup[loc.state].colorString
    }
  }

  public addSubscriber (sub: Notify) {
    this._subscribers.push(sub)
  }

  protected _notifySubscribers (subs: Array<Notify>, source: Notify) {
    for (let idx = 0; idx < subs.length; idx++) {
      if (subs[idx] !== source) {
        subs[idx]()
      }
    }
    source()
  }

  public locationRadius (loc: LocationData): number {
    // If we are on the highlighted state, or using a state based display
    if ((this._locationHighlight === loc.id)) {
      return 6
    }
    const focus = this.isFocused(loc)
    if (focus === FocusLevel.state) {
      return 5
    } else if (loc.isState) {
      return 3
    }
    return 1
  }

  private _clearThrottle () {
    console.log('Clear throttle')
    console.log(HighlightData._lastNotify)
    HighlightData._lastNotify = null
    console.log(HighlightData._lastNotify)
  }

  private _throttleNotify (source: Notify) {
    if (HighlightData._lastNotify === null) {
      HighlightData._lastNotify = source
      this._notifySubscribers(this._subscribers, source)
      setTimeout(this._clearThrottle, 200)
    } else {
      console.log(HighlightData._lastNotify)
    }
  }

  private isFocused (loc: LocationData): FocusLevel {
    if (typeof this._stateGroup[loc.state] !== 'undefined') {
      return FocusLevel.state
    }
    return FocusLevel.none
  }

  public handleHover (loc: LocationData, source: Notify) {
    if ((loc.id !== this._locationHighlight) || (loc.state !== this._stateHighlight)) {
      if (loc.id === loc.state) {
        this._stateHighlight = loc.state // to fix
      } else {
        this._locationHighlight = loc.id
      }

      this._throttleNotify(source)
    }
  }

  public toggleLoc (loc: LocationData, source: Notify) {
    if (typeof this._stateGroup[loc.state] !== 'undefined') {
      delete this._stateGroup[loc.state]
    } else {
      this._stateGroup[loc.state] = { rgb: palette[this._lastColor], colorString: paletteToRGB(palette[this._lastColor]) }
      this._lastColor = (this._lastColor + 1) % palette.length
    }
    this._throttleNotify(source)
  }

  public stateList () {
    const retVal = []
    const stateList = this._locations.stateList()
    for (let idx = 0; idx < stateList.length; idx++) {
      const curLocation = stateList[idx]
      const highlight = (typeof this._stateGroup[curLocation.state] !== 'undefined') ? this._stateGroup[curLocation.state] : { rgb: gray, colorString: grayString }
      retVal.push({
        location: curLocation,
        highlight: highlight
      })
    }
    return retVal
  }
}
