<template>
    <canvas :id="chartId"></canvas>
</template>

<script lang="ts">

import { defineComponent } from 'vue'
import { Chart, ChartTooltipItem, ChartData } from 'chart.js'
import { ChartSet, ReferenceLine } from '@/assets/CovidData'
import { LocationsMap, LocationData } from '@/assets/LocationsMap'
import { HighlightData } from '@/assets/HighlightSystem'

function roundExponent (value: number) {
  const orderOfMagnitude = Math.pow(10, Math.floor(Math.log10(value)))
  const msd = Math.floor(value / orderOfMagnitude)
  return msd * orderOfMagnitude
}

function roundUpExponent (value: number) {
  const orderOfMagnitude = Math.pow(10, Math.floor(Math.log10(value) - 1))
  const msd = Math.floor(value / orderOfMagnitude + 1)
  return msd * orderOfMagnitude
}

function formatTicks (value: number, index: number, values: Array<number>) {
  if (value > 0) {
    const mostSigDigit = value / Math.pow(10, Math.floor(Math.log10(value)))
    if ((((index > 0) && (index < values.length - 2))) && ((mostSigDigit === 9) || (mostSigDigit === 8) || (mostSigDigit === 7) || (mostSigDigit === 6) || (mostSigDigit === 5) || (mostSigDigit === 3))) {
      return ''
    }
    const twoDigits = value / Math.pow(10, Math.floor(Math.log10(value) - 1))
    if ((twoDigits % 10) > 0) {
      return ''
    }
  }
  return value.toString()
}
class ReferenceLineStyle {
  dash: Array<number>;
  color: string;
  constructor (dash: Array<number>, r: number, g: number, b: number) {
    this.dash = dash
    this.color = 'rgba(' + r + ',' + g + ',' + b + ')'
  }
}
const referenceStyles = [
  new ReferenceLineStyle([5, 2, 2, 7], 20, 100, 20),
  new ReferenceLineStyle([4, 5, 6, 3], 50, 150, 200),
  new ReferenceLineStyle([3, 7, 1, 5], 200, 100, 100)
]
export default defineComponent({
  name: 'CovidScatter',
  props: {
    chartId: {
      type: String,
      required: true
    },
    chartData: {},
    referenceLines: {},
    highlightSet: {}
    // TODO: Include Reference Lines
  },
  data: function () {
    return {
      chart: {},
      locMap: {},
      domainRangeData: { domainMin: 0, domainMax: 0, rangeMin: 0, rangeMax: 0 }
    }
  },
  // TODO: Highlight parameters
  methods: {
    getLocationID (dataIndex: number): number {
      const myData = this.chartData as ChartSet
      return myData.locations[dataIndex]
    },
    lookupLocation (locationID: number) {
      const map = this.locMap as LocationsMap
      return map.lookup(locationID)
    },
    // ignore warnings about unused parameters, needed to match signature of Chart.js
    // eslint-disable-next-line
    labelFn (tooltipItem: ChartTooltipItem, data: ChartData) {
      const pointIndex = tooltipItem.index as number
      const value = Number(tooltipItem.value)
      const location = this.lookupLocation(this.getLocationID(pointIndex)) as LocationData
      const perCapita = Math.round(value / location.population * 100000 * 10) / 10
      // this.highlightObject().handleHover(location, this.notify)
      return location.name + '|' + value + '|' + perCapita
    },
    highlightObject () {
      return this.highlightSet as HighlightData
    },
    contextLocation (context: Record<string, unknown>) {
      const pointLocation = this.getLocationID(context.dataIndex as number)
      const loc = this.lookupLocation(pointLocation) as LocationData
      return loc
    },
    pointColor (context: Record<string, unknown>) {
      return this.highlightObject().locationColor(this.contextLocation(context))
    },
    pointRadius (context: Record<string, unknown>) {
      return this.highlightObject().locationRadius(this.contextLocation(context))
    },
    borderWidth (context: Record<string, unknown>) {
      const loc = this.contextLocation(context)
      return 1
    },
    notify () {
      const cht = this.chart as Chart
      cht.update()
    },
    chartOptions (xlabel: string, ylabel: string) {
      const chartoptions = {
        animation: {
          duration: 0
        },
        // aspectRatio: 1.25,
        responsive: true,
        scales: {
          xAxes: [{
            type: 'logarithmic',
            display: true,
            ticks: {
              callback: formatTicks,
              min: roundExponent(this.domainRangeData.domainMin),
              max: roundUpExponent(this.domainRangeData.domainMax)
            },
            scaleLabel: {
              display: true,
              labelString: xlabel
            }
          }],
          yAxes: [{
            type: 'logarithmic',
            ticks: {
              callback: formatTicks
            },
            scaleLabel: {
              display: true,
              labelString: ylabel
            }
          }]
        },
        tooltips: {
          callbacks: {
            label: this.labelFn
          }
        },
        hover: {
          animationDuration: 0
        }
      }
      return chartoptions
    },
    domainRange (chartData: ChartSet) {
      // if(this.domainRangeData)
      let point = chartData.data[0]
      const retVal = {
        domainMin: point.x,
        domainMax: point.x,
        rangeMin: point.y,
        rangeMax: point.y
      }
      for (let idx = 1; idx < chartData.data.length; idx++) {
        point = chartData.data[idx]
        if (retVal.domainMin > point.x) { retVal.domainMin = point.x }
        if (retVal.domainMax < point.x) { retVal.domainMax = point.x }
        if (retVal.rangeMin > point.y) { retVal.rangeMin = point.y }
        if (retVal.rangeMax < point.y) { retVal.rangeMax = point.y }
      }
      this.domainRangeData.domainMin = retVal.domainMin
      this.domainRangeData.domainMax = retVal.domainMax
      this.domainRangeData.rangeMin = retVal.rangeMin
      this.domainRangeData.rangeMax = retVal.rangeMax

      return retVal
    },
    scaleRefLine (slope: number) {
      const yMin: number = this.domainRangeData.rangeMin < 1 ? 1 : this.domainRangeData.rangeMin
      const x1a = yMin / slope
      const lineData = []
      let x: number
      let y: number
      if ((this.domainRangeData.rangeMin <= 1) && (x1a >= this.domainRangeData.domainMin)) {
        x = x1a
        y = yMin
      } else {
        x = this.domainRangeData.domainMin
        y = this.domainRangeData.domainMin * slope
      }
      lineData.push({ x: x, y: y })
      // compute second point
      const y2a = this.domainRangeData.domainMax * slope
      if (y2a > this.domainRangeData.rangeMax) {
        x = this.domainRangeData.rangeMax / slope
        y = this.domainRangeData.rangeMax
      } else {
        x = this.domainRangeData.domainMax
        y = this.domainRangeData.domainMax * slope
      }
      lineData.push({ x: x, y: y })
      return lineData
    },
    formatChartDataForChartJs (chartdata: ChartSet, refLines: Array<ReferenceLine>) {
      const datasets = []
      const scatter = {
        data: chartdata.data,
        pointStyle: 'crossRot' as 'crossRot', // TODO: Custom by state?
        pointBackgroundColor: this.pointColor,
        pointBorderColor: this.pointColor,
        pointRadius: this.pointRadius,
        label: chartdata.day,
        pointHoverRadius: 3,
        borderWidth: this.borderWidth
      }
      datasets.push(scatter)
      for (let idx = 0; idx < refLines.length; idx++) {
        const refLine = refLines[idx]
        const labels = new Array<number>()

        // TODO: Scale reference lines to domain or range? min y, and max X?
        const refPoints = this.scaleRefLine(refLine.slope)
        const label: string = refLine.label
        const lineStyle = referenceStyles[idx % referenceStyles.length]
        const chartLine = { type: 'line', data: refPoints, labels: labels, label: label, fill: false, pointRadius: 0, pointHitRadius: 0, borderColor: lineStyle.color, borderDash: lineStyle.dash, borderWidth: 2 }
        datasets.push(chartLine)
      }
      return datasets
    }
  }, // close methods
  async mounted () {
    console.log('In Scatter')

    const ctx = document.getElementById(this.chartId) as HTMLCanvasElement
    const chartdata = this.chartData as ChartSet
    this.domainRange(chartdata)
    this.locMap = await LocationsMap.getInstance()
    const hs = this.highlightSet as HighlightData
    hs.addSubscriber(this.notify)

    const refLines = this.referenceLines as Array<ReferenceLine>

    this.chart = new Chart(ctx, { type: 'scatter', data: { datasets: this.formatChartDataForChartJs(chartdata, refLines) }, options: this.chartOptions(chartdata.xlabel, chartdata.ylabel) })

    // this.chart = new Chart(ctx, { type: 'scatter', data: { datasets: [{ data: chartdata.data, pointBackgroundColor: this.pointColor, pointRadius: 2, label: chartdata.day }] }, options: this.chartOptions(chartdata.xlabel, chartdata.ylabel) })
  }
})
</script>
// TODO: Highlight across charts
