<template>
  <div class="home">
    <button @click="draw">Hello</button>
    <div v-for="( item, index ) in scatterData" :key='index' class="chartbox">
      <CovidScatter ref='test' v-bind:chart-id='"test" + index' v-bind:chartData='item.scatter' v-bind:highlightSet='highlightSet' v-bind:referenceLines='item.referenceLines'></CovidScatter>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import CovidScatter from '@/components/CovidScatter.vue' // @ is an alias to /src
import { DataFactory, ScatterChartComponents, ReferenceLine } from '@/assets/CovidData'
import { HighlightData } from '@/assets/HighlightSystem'
import { LocationsMap } from '@/assets/LocationsMap'

export default defineComponent({
  name: 'Display',
  components: {
    CovidScatter
  },
  data () {
    return {
      scatterData: new Array<ScatterChartComponents>(),
      highlightSet: {}
    }
  },
  methods: {
    draw: async function () {
      const locationsMap = await LocationsMap.getInstance()
      this.highlightSet = new HighlightData(locationsMap)

      const df = DataFactory.getInstance()
      await df.getAsOf()
      let info = await df.getPopDataSet('deaths', '_delta_ma_7')
      let referenceLines: Array<ReferenceLine> = await df.getReferenceLines('deaths', '_delta_ma_7')
      const components = { scatter: info, referenceLines: referenceLines }
      this.scatterData.push(components)

      info = await df.getPopDataSet('confirmed', '_delta_ma_7')
      referenceLines = await df.getReferenceLines('confirmed', '_delta_ma_7')
      const components2 = { scatter: info, referenceLines: referenceLines }
      this.scatterData.push(components2)

      info = await df.getPopDataSet('confirmed_states', 'total')
      referenceLines = await df.getReferenceLines('confirmed_states', 'total')
      const components3 = { scatter: info, referenceLines: referenceLines }
      this.scatterData.push(components3)

      info = await df.getPopDataSet('confirmed', 'total')
      referenceLines = await df.getReferenceLines('confirmed', 'total')
      const components4 = { scatter: info, referenceLines: referenceLines }
      this.scatterData.push(components4)
    },
    getData: async function () {
      return 'nop'
    }
  },
  mounted () {
    this.draw()
  }
})
</script>

