export interface LocationData
{
  id: number;
  name: string;
  population: number;
  state: number;
  popRank: number;
  isState: boolean;
}
const external = 'https://raw.githubusercontent.com/yehster/covid-data-prelim/main/locations'
const internal = 'http://192.168.1.200/covidcache/locations'
async function getLocationData () {
  const response = await fetch(external, { cache: 'no-cache' })
  if (!response.ok) {
    const retry = await fetch('http://192.168.1.200:8081/locations')
    return await retry.json()
  }
  const map = await response.json()
  return map
}

// Singleton Class
export class LocationsMap {
  private static instance: LocationsMap
  private static loading = false
  private static stateList: Array<LocationData>
  private static mapData: Record<number, LocationData>

  private constructor () {
    LocationsMap.stateList = new Array<LocationData>()
    for (const key in LocationsMap.mapData) {
      const loc = LocationsMap.mapData[key]
      if (loc.isState) {
        LocationsMap.stateList.push(loc)
      }
    }
    console.log('Locations Map Created')
  }

  public static getInstance (): Promise<LocationsMap> {
    return new Promise((resolve, reject) => {
      if (!LocationsMap.instance) {
        if (!LocationsMap.loading) {
          LocationsMap.loading = true
          getLocationData().then(mapData => {
            LocationsMap.mapData = mapData
            LocationsMap.instance = new LocationsMap()
          })
        } else {
          const isLoaded = function () {
            if (!LocationsMap.instance) {
              console.log('waiting for map')
              setTimeout(isLoaded, 100)
            } else {
              console.log('wait completed')
              resolve(LocationsMap.instance)
            }
          }
          isLoaded()
        }
      } else {
        resolve(LocationsMap.instance)
      }
    })
  }

  lookup (location: number): LocationData | null {
    if (LocationsMap.mapData) {
      return LocationsMap.mapData[location]
    } else {
      return null
    }
  }

  public stateList () {
    return LocationsMap.stateList
  }
}

// Initialize
LocationsMap.getInstance()
