
import { defineComponent } from 'vue'
import { LocationsMap, LocationData } from '@/assets/LocationsMap'
import { HighlightData, HighlightLocation } from '@/assets/HighlightSystem'

export default defineComponent({
  name: 'StatsDisplay',
  props: {
    highlightSet: {},
    locMap: {}
  },
  computed: {
    displayList (): Array<HighlightLocation> {
      return this.highlightList()
    }
  },
  methods: {
    itemStyle (item: HighlightLocation) {
      console.log(item.highlight)
      return {
        color: item.highlight.colorString
      }
    },
    locations () {
      const loc = this.locMap as LocationsMap
      return loc
    },
    highlightList () {
      const hs = this.highlightSet as HighlightData
      if (hs.stateList !== undefined) {
        return hs.stateList()
      }
      return []
    },
    toggle (item: HighlightLocation) {
      const hs = this.highlightSet as HighlightData
      hs.toggleLoc(item.location, this.notify)
    },
    notify () {
      console.log('notify')
      this.$forceUpdate()
    },
    async mounted () {
      const hs = this.highlightSet as HighlightData
      hs.addSubscriber(this.notify)
    }
  }

})
